$color-primary: #fff;
$color-secondary: $prime;
.loading {
    position: fixed !important;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 99999999999999999 !important;
    background-color: $prime;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loader {
    &__text {
        margin: 5px auto;
        color: $color-primary;
        text-transform: uppercase;
        letter-spacing: 1px;
    }
    &__filmstrip {
        position: relative;
        width: 56px;
        height: 70px;
        background: $color-primary;
        z-index: -1;
        &:before,
        &:after {
            content: '';
            position: absolute;
            height: 120%;
            border-left: 5px dashed $color-secondary;
            animation: roll 20ms infinite;
        }
        &:before {
            left: 5px;
        }
        &:after {
            right: 5px;
        }
    }
}

@keyframes roll {
    0% {
        top: 0px;
    }
    100% {
        top: -15px;
    }
}

